
import { Component, OnInit } from '@angular/core';

import { DataMethod } from '../Model/DataMethod';
import { PostService } from '../Service/post.service';



@Component({
  selector: 'app-methode',
  templateUrl: './methode.component.html',
  styleUrls: ['./methode.component.css']
})
export class MethodeComponent implements OnInit {

  Methods = [0, 1, 2, 3, 4, 5];
  methodSelected: String;
  alpha: Number;
  nbObjet: Number;
  result: DataMethod;

  valeura: Number;
  valeurc: Number;

  constructor(private service: PostService) { }

  ngOnInit() {
    this.result = new DataMethod();
    this.result.a = new Array<Number>();
    this.result.c = new Array<Number>();
  }

  GenererRandom() {
    if ( this.alpha.toString().length > 0 && this.nbObjet.toString().length > 0 ) {
      this.service.getRandom(this.alpha, this.nbObjet).subscribe(
        data => { this.result = data; },
        errors => { alert(JSON.stringify(errors)); }
      );
    }

  }

  Add() {
    this.result.a.push(this.valeura);
    this.result.c.push(this.valeurc);
  }

  Delete(i: Number) {
    this.result.a.splice(i.valueOf(), 1);
    this.result.c.splice(i.valueOf(), 1);
  }

  CalculMethod() {

    switch (this.methodSelected) {
      case 'Methode Emmun√©ration totale' :
            this.result.numMethod = 1;
            this.result.result = new Array<Number>();
            this.result.bi = 0;
            this.result.bs = 0;
            this.result.cout = 0;
      break;
      case `Branch and Bound` :
      this.result.numMethod = 2;
      this.result.result = new Array<Number>();
      this.result.bi = 0;
      this.result.bs = 0;
      this.result.cout = 0;
            break;
      case `Methode dynamique dense` :
      this.result.numMethod = 3;
      this.result.result = new Array<Number>();
      this.result.bi = 0;
            this.result.bs = 0;
            this.result.cout = 0;
        break;
      case `Heuristique Gloutonne` :
      this.result.numMethod = 4 ;
      this.result.result = new Array<Number>();
      this.result.bi = 0;
            this.result.bs = 0;
            this.result.cout = 0;
        break;
      case `Relaxation en continu` :
      this.result.numMethod = 5 ;
      this.result.result = new Array<Number>();
      this.result.bi = 0;
      this.result.bs = 0;
      this.result.cout = 0;
        break;
      default:
        return 0;
    }

    this.service.Calcul(this.result).subscribe(
      data => { this.result = data; },
      errors => { alert(JSON.stringify(errors)); }
    );
  }

}



